import { createDraftSafeSelector as createSelector } from '@reduxjs/toolkit'
import createCachedSelector from 're-reselect'

import { getOptionListAsList, getOptionName } from '../../options/selector'
import camelCase from 'lodash/camelCase'

const getState = (state) => state.lo

export const getLoOptions = createSelector(getState, lo => lo.options)

export const getLoOptionValueByName = createCachedSelector(
  [getLoOptions, getOptionListAsList, getOptionName],
  (loOptions, optionsList, optionName) => {
    if (!loOptions || !optionsList || !optionName) {
      return
    }

    const { loOption, option } = getOptionObjects(loOptions, optionsList, optionName)
    if (!option) {
      return
    }

    if (loOption) {
      return loOption.value || loOption.key
    } else {
      return option.values.first().key
    }
  }
)(getOptionName)

export const getLoOptionKeyByName = createCachedSelector(
  [getLoOptions, getOptionListAsList, getOptionName],
  (loOptions, optionsList, optionName) => {
    if (!loOptions || !optionsList || !optionName) {
      return
    }

    const { loOption, option } = getOptionObjects(loOptions, optionsList, optionName)
    if (!option) {
      return
    }

    if (loOption) {
      return loOption.key
    } else {
      return option.values.first().key
    }
  }
)(getOptionName)

const getOptionObjects = (loOptions, optionsList, optionName) => {
  let option = optionsList.find(op => camelCase(op.name) === camelCase(optionName))
  let loOption = loOptions.get(option.id.toString())
  return {
    option,
    loOption
  }
}
