import React from 'react'
import PropTypes from 'prop-types'
import ICCrosswordClueList from './ICCrosswordClueList.jsx'
import { ICCrosswordClues as ICCrosswordCluesUI } from '../ui.jsx'

const ICCrosswordClues = ({ activityId, screenId, cluesInColumns }) => {

  const getClueListByDirection = (direction, index) => (
    <ICCrosswordClueList
      key={index}
      align={direction}
      activityId={activityId}
      screenId={screenId}/>
  )

  return (
    <ICCrosswordCluesUI 
      cluesInColumn={cluesInColumns}>
      {['h', 'v'].map(getClueListByDirection)}
    </ICCrosswordCluesUI>
  )
}

ICCrosswordClues.propTypes = {
  activityId: PropTypes.string,
  screenId: PropTypes.number,
  cluesInColumns: PropTypes.bool
}

export default ICCrosswordClues
