import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { ICCrosswordHighlightContext } from '../../context'
import { getActivityItemData } from '../../../../itemData/selector/general'

const ICCrosswordHighlightManager = ({ words, children }) => {
  const [highlightedCells, setHighlightedCells] = useState([])
  const [highlightedWords, setHighlightedWords] = useState([])

  const mouseOverEvent = (ids) => {
    setHighlightedCells(
      words
        .filter(({ id }) => ids.includes(id))
        .map(({ cells }) => cells)
        .flat()
    )
    setHighlightedWords(ids)
  }

  const mouseOutEvent = () => {
    setHighlightedCells([])
    setHighlightedWords([])
  }

  return (
    <ICCrosswordHighlightContext.Provider value={{
      highlighted: highlightedCells,
      highlightedWords: highlightedWords,
      mouseOverEvent,
      mouseOutEvent
    }}>
      {children}
    </ICCrosswordHighlightContext.Provider>
  )
}

ICCrosswordHighlightManager.propTypes = {
  words: PropTypes.array.isRequired,
  children: PropTypes.node
}

const mapStateToProps = (state, ownProps) => ({
  words: getActivityItemData(state, ownProps)
})

export default connect(
  mapStateToProps
)(ICCrosswordHighlightManager)
