import { select } from 'redux-saga/effects'
import { normalizeCells } from '../../schema'
import { normalizeSolutionData } from '../../../../solutionData/schema'
import { processors as activitiesProcessors } from '../../../../activities/saga/processor'

import { actions } from '../../reducer'

import { getActivityCells } from '../../selector/cellsItemData'
import { getActivityCellsUserData } from '../../selector/cellsUserData'

export function* addActivities(payload) {
  const { id, metadata, solutions, items } = payload
  const { cells } = metadata

  const mappedItemCells = cells.map(cell => ({
    id: `${id}_${cell.x}_${cell.y}`,
    activityId: id,
    ...cell,
    words: cell.words.map(w => `${id}_${w}`)
  }))

  let changedItems = items.map(word => ({
    ...word,
    cells: word.cells.map(cell => mappedItemCells[cell].id)
  }))

  let changedMetadata = { ...metadata }
  delete changedMetadata['cells']

  const list = yield activitiesProcessors.addActivities({
    ...payload,
    items: changedItems,
    metadata: changedMetadata
  })
  list.push(actions.addCellsItemData({
    data: normalizeCells(mappedItemCells).entities.cellsItemData
  }))

  if (solutions) {
    const mappedSolutionCells = cells.map(cell => {
      const refWord = items[cell.words[0]]
      const cellIndexInWord = refWord.cells.indexOf(cell.index)

      return {
        id: `${id}_${cell.x}_${cell.y}`,
        activityId: id,
        value: solutions[refWord.index].value.charAt(cellIndexInWord)
      }
    })

    list.push(actions.addCellsSolutionData({
      data: normalizeSolutionData(mappedSolutionCells).entities.solutionData
    }))
  }
  return list
}

export function* removeActivities(payload) {
  const { id, activityType } = payload

  const cells = yield select(getActivityCells, {
    activityType,
    activityId: id
  })

  const cellsIds = cells.map(({ id }) => id)

  const userDataCells = yield select(getActivityCellsUserData, {
    activityType,
    activityId: id
  })

  const userDataCellsIDs = userDataCells.map(({ id }) => id)

  const ret = yield activitiesProcessors.removeActivities(payload)

  ret.push(actions.clearCellsItemData({
    data: cellsIds
  }))

  ret.push(actions.clearCellsSolutionData({
    data: cellsIds
  }))

  ret.push(actions.clearCellsUserData({
    data: userDataCellsIDs
  }))

  ret.push(actions.clearCellUIData({
    data: cellsIds
  }))

  return ret
}
