import React from 'react'
import PropTypes from 'prop-types'

import ICCrosswordRow from './ICCrosswordRow.jsx'

import { ICCrosswordGrid as ICCrosswordGridUI } from './ui.jsx'

const ICCrosswordGrid = ({ 
  rows,
  cols,
  activityId,
  screenId
}) => {
  let rowComps = []
  for (let i = 0; i < rows; i++) {
    rowComps.push(
      <ICCrosswordRow
        key={i}
        index={i}
        cols={cols}
        activityId={activityId}
        screenId={screenId}
      />
    )
  }

  return (
    <ICCrosswordGridUI>
      {rowComps}
    </ICCrosswordGridUI>
  )
}

ICCrosswordGrid.propTypes = {
  rows: PropTypes.number.isRequired,
  cols: PropTypes.number.isRequired,
  activityId: PropTypes.string.isRequired,
  screenId: PropTypes.number.isRequired
}

export default ICCrosswordGrid
