import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import {
  ICCrosswordCellClue as ICCrosswordCellClueUI,
  ICCrosswordClueInCell as ICCrosswordClueInCellUI
} from '../ui.jsx'

import { ICCrosswordHighlightContext } from '../../context'

import { getCellWords } from '../../selector/cellsItemData'

const ICCrosswordCellClue = ({ itemData }) => {
  const getWordByAlign = (align) => itemData.find((w) => w.align === align)

  return (
    <ICCrosswordHighlightContext.Consumer>
      {(highlightContext) => (
        <ICCrosswordCellClueUI
          horizontalId={getWordByAlign('h')?.id}
          verticalId={getWordByAlign('v')?.id}
          onMouseOver={() => {
            highlightContext.mouseOverEvent(itemData.map(({ id }) => id))
          }}
          onMouseOut={() => {
            highlightContext.mouseOutEvent()
          }}
        >
          {itemData.map((w, i) => (
            <ICCrosswordClueInCellUI
              key={i}
              clue={w.clue}
              active={false}
              orientation={w.align === 'h' ? 'horizontal' : 'vertical'}
            />
          ))}
        </ICCrosswordCellClueUI>
      )}
    </ICCrosswordHighlightContext.Consumer>
  )
}

ICCrosswordCellClue.propTypes = {
  index: PropTypes.number,
  itemData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      cells: PropTypes.array,
      align: PropTypes.string
    })
  )
}

const mapStateToProps = (state, ownProps) => ({
  itemData: getCellWords(state, ownProps)
})

export default connect(mapStateToProps)(ICCrosswordCellClue)
