import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import { getItemsByAlign } from '../../selector/general'

import { ICCrosswordClueList as ICCrosswordClueListUI } from '../ui.jsx'
import ICCrosswordClue from './ICCrosswordClue.jsx'


const ICCrosswordClueList = ({
  align,
  clues
}) => {
  const getClueComp = (clue, idx) => (
    <ICCrosswordClue
      key={idx}
      {...clue}/>
  )
  return (
    <ICCrosswordClueListUI 
      orientation={align === 'v' ? 'vertical' : 'horizontal'}
    >
      {clues.map(getClueComp)}
    </ICCrosswordClueListUI>
  )
}

ICCrosswordClueList.propTypes = {
  align: PropTypes.string,
  clues: PropTypes.array
}

const mapStateToProps = (state, ownProps) => ({
  clues: getItemsByAlign(state, ownProps)
})

export default connect(
  mapStateToProps
)(ICCrosswordClueList)
