import createCachedSelector from 're-reselect'
import { createArraySelector } from '../../../../selector/utils'

import { cluePositionIsInside } from '../../../screens/selector/optionValues'

import { getActivityByActivityId } from '../../../activities/selector'
import { getActivityItemData } from '../../../itemData/selector/general'
import { getActivityUIDataDataTypeSolution } from '../../../uiData/selector/general'
import { getActivityUserData } from '../../../userData/selector/general'

const getActivityId = (state, props) => props.activityId
const getAlign = (state, props) => props.align 

export const getICCrosswordColSize = createCachedSelector(
  [getActivityByActivityId, cluePositionIsInside],
  (activity, cluesAreInside) => {
    if (!activity) {
      return
    }
    return activity.metadata.colSize + (cluesAreInside ? 1 : 0)
  }
)(getActivityId)

export const getICCrosswordRowSize = createCachedSelector(
  [getActivityByActivityId, cluePositionIsInside],
  (activity, cluesAreInside) => {
    if (!activity) {
      return
    }
    return activity.metadata.rowSize + (cluesAreInside ? 1 : 0)
  }
)(getActivityId)

export const getItemsByAlign = createCachedSelector(
  [getActivityItemData, getAlign],
  (items, align) => {
    if (!align || !items) {
      return
    }
    return items.filter(it => it.align === align)
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getActivityItemDataFilteredByFilled = createCachedSelector(
  [getActivityItemData, getActivityUIDataDataTypeSolution, getActivityUserData],
  (words, solutionTypeWordUIdata, userData) => {
    if (!words || !userData) {
      return
    }
    const solutionTypeWordIds = solutionTypeWordUIdata.map(({ id }) => id)
    const solutionTypeWords = words.filter(({ id }) => solutionTypeWordIds.includes(id))
    const solutionTypeCells = [...new Set(solutionTypeWords.map(({ cells }) => cells).flat())]
    const filledWords = words
      .filter(word => {
        const wUserData = userData.find(w => w.id === word.id)
        if (!wUserData || !wUserData.value?.trim()) {
          return 
        }
        const isFilled = wUserData.value.split('').filter((char, idx) => {
          return solutionTypeCells.includes(word.cells[idx]) || 
            char.trim()
        }).length === word.cells.length
        return isFilled && wUserData.value.trim()
      })
    return filledWords
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getActivityUserDataFilled = createCachedSelector(
  [getActivityUserData, getActivityItemDataFilteredByFilled],
  (activityUserData, activityItemsFiltered) => {
    if (!activityItemsFiltered) {
      return
    }
    return activityItemsFiltered.map(({ id }) => activityUserData.find(ud => ud.id === id))
  }
)(getActivityId, { selectorCreator: createArraySelector })

