import createCachedSelector from 're-reselect'
import { createArraySelector } from '../../../../selector/utils'

import { getActivityValidationData } from '../../../validationData/selector/general'

const getId = (state, props) => props.id
const getWords = (state, props) => props.words

export const getCellWordsValidationData = createCachedSelector(
  [getWords, getActivityValidationData],
  (cellWords, validationData) => {
    if (!cellWords?.length || !validationData) {
      return
    }
    return validationData.filter(v => cellWords.includes(v.id)) 
  }
)(getId, { selectorCreator: createArraySelector })
