import { createSlice } from '@reduxjs/toolkit'
import { actions as cellsItemActions } from './cellsItemData'
import { model } from '../../../uiData/reducer/processor'

const slice = createSlice({
  name: 'iccrossword_cell_uiData',
  initialState: {},
  reducers: {
    setCellUIData: (state, action) => {
      const cells = action.payload.data
      cells.forEach(el => {
        state[el.id] = {
          ...state[el.id],
          ...el
        }
      })
    },
    clearCellUIData: (state, action) => {
      const cells = action.payload.data

      const newState = Object.keys(state).reduce((acc, id) => {
        if (!cells.includes(id)) {
          acc[id] = state[id]
        }
        return acc
      }, {})
      return newState
    }
  },
  extraReducers: {
    [cellsItemActions.addCellsItemData.type]: (state, { payload: { data } }) => {
      const els = Object.values(data).reduce((acc, val) => {
        acc[val.id] = {
          id: val.id,
          activityId: val.activityId,
          enabled: model.enabled,
          dataType: model.dataType
        }
        return acc
      }, {})

      return {
        ...state,
        ...els
      }
    }
  }
})

export const reducer = slice.reducer
export const actions = slice.actions
