import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import ICCrosswordCellInput from './Cells/ICCrosswordCellInput.jsx'
import ICCrosswordCellClue from './Cells/ICCrosswordCellClue.jsx'

import {
  ICCrosswordRow as ICCrosswordRowUI,
  ICCrosswordCellInvisible as ICCrosswordCellInvisibleUI
} from './ui.jsx'

import { getActivityCellsByRow } from '../selector/cellsItemData'
import { getCluePosition } from '../../../screens/selector/index.js'

const ICCrosswordRow = ({ cells, activityId, screenId }) => {
  const cellComps = cells.map((cell, i) => {
    if (!cell) {
      return <ICCrosswordCellInvisibleUI key={i} />
    } else if (cell.id) {
      return (
        <ICCrosswordCellInput
          key={i}
          {...cell}
          screenId={screenId}
          activityId={activityId}
        />
      )
    } else {
      return (
        <ICCrosswordCellClue
          key={i}
          {...cell}
          screenId={screenId}
          activityId={activityId}
        />
      )
    }
  })
  return <ICCrosswordRowUI>{cellComps}</ICCrosswordRowUI>
}

ICCrosswordRow.propTypes = {
  cells: PropTypes.array.isRequired,
  activityId: PropTypes.string.isRequired,
  screenId: PropTypes.number.isRequired
}

const mapStateToProps = (state, props) => ({
  cells: getActivityCellsByRow(state, props),
  cluesAreInside: getCluePosition(state, props)
})

export default connect(mapStateToProps)(ICCrosswordRow)
