import { createDraftSafeSelector as createSelector } from '@reduxjs/toolkit'
import createCachedSelector from 're-reselect'
import camelCase from 'lodash/camelCase'

import { getScreen } from './data'
import { getOptionListAsList, getOptionName } from '../../options/selector'
import { getLoOptions } from '../../lo/selector'

const getScreenOptions = createSelector(
  [getScreen], 
  (screen) => screen.options)

export const getScreenOptionValueByName = createCachedSelector(
  [getScreenOptions, getLoOptions, getOptionListAsList, getOptionName],
  (screenOptions, loOptions, optionsList, optionName) => {
    if (!screenOptions || !loOptions || !optionsList || !optionName) {
      return
    }

    const { screenOption, loOption, option } = getOptionObjects(screenOptions, loOptions, optionsList, optionName)
    if (!option) {
      return
    }

    if (screenOption) {
      return screenOption.value || screenOption.key
    } else if (loOption) {
      return loOption.value || loOption.key
    } else {
      return option.values.first().key
    }
  }
)(getOptionName)

export const getScreenOptionKeyByName = createCachedSelector(
  [getScreenOptions, getLoOptions, getOptionListAsList, getOptionName],
  (screenOptions, loOptions, optionsList, optionName) => {
    if (!screenOptions || !loOptions || !optionsList || !optionName) {
      return
    }
    const { screenOption, loOption, option } = getOptionObjects(screenOptions, loOptions, optionsList, optionName)
    if (!option) {
      return
    }
    if (screenOption) {
      return screenOption.key
    } else if (loOption) {
      return loOption.key
    } else {
      return option.values.first().key
    }
  }
)(getOptionName)

const getOptionObjects = (screenOptions, loOptions, optionsList, optionName) => {
  let option = optionsList.find(op => camelCase(op.name) === camelCase(optionName))
  let loOption = loOptions[option.id.toString()]
  let screenOption = screenOptions[option.id.toString()]
  return {
    option,
    loOption,
    screenOption
  }
}
