import { dispatchEvent } from '../../../utils/actions/eventManager'
import { eventConstants } from '../../activities/action'
import { registerActivityType, getActivityTypeIsRegistered } from '../../../utils/registerActivityType'

import saga from './saga'
import reducer from './reducer'

import { registerProcessors as registerSagaProcessors } from './saga/processor'
import { registerProcessors as registerSelectorProcessors } from './selector/processor'

const RegisterActivity = ({ activityType, activityId }) => {
  if (!getActivityTypeIsRegistered(activityType)) {
    registerSagaProcessors()
    registerSelectorProcessors()
  
    registerActivityType({
      activityType,
      saga,
      reducer
    })  
  }
  
  dispatchEvent(eventConstants.READY, {
    type: activityType, 
    id: activityId 
  })
}

export default RegisterActivity
