import createCachedSelector from 're-reselect'

import { createArraySelector } from '../../../../selector/utils'

import { getCellUIDataById } from './cellsUIData'

const getId = (state, props) => props.id
const getActivityId = (state, props) => props.activityId 
const getCellsSolutionData = (state) => state.iccrossword_cells_solutionData

const getCellsSolutionDataAsList = createArraySelector(
  [getCellsSolutionData],
  (cells) => {
    if (!cells) {
      return
    }
    return Object.values(cells)
  }
)

export const getActivityCellsSolutionData = createCachedSelector(
  [getCellsSolutionDataAsList, getActivityId],
  (cells, activityId) => {
    if (!cells || !activityId) {
      return
    }

    return cells.filter(cell => cell.activityId === activityId)
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getCellSolutionData = createCachedSelector(
  [getId, getCellsSolutionData, getCellUIDataById],
  (id, solutionDataCells, uiData) => {
    if (!id || !uiData || uiData.dataType === 'user') {
      return
    }
    return solutionDataCells[id]?.value
  }
)(getId)
