import { select } from 'redux-saga/effects'
import { actions as validationDataActions } from '../../../../validationData/reducer'
import { normalizeValidationData } from '../../../../validationData/schema'

import { getItemDataListByIds } from '../../../../itemData/selector/general'
import { getSolutionDataListByIds } from '../../../../solutionData/selector/general'
import { getUserDataListByIds } from '../../../../userData/selector/general'

import { getSolutionDataTypeCells } from '../../selector/cellsUIData'

export function* validate(payload) {
  const { activityId } = payload

  const itemData = yield select(getItemDataListByIds, payload)
  const solutionData = yield select(getSolutionDataListByIds, payload)
  const userData = yield select(getUserDataListByIds, payload)
  
  let solutionDataTypeCells = yield select(getSolutionDataTypeCells, payload)
  solutionDataTypeCells = solutionDataTypeCells.map(c => c.id)

  if (itemData.length) {
    const validationData = itemData.map(item => {
      const wUserDataStored = userData.find(us => us.id === item.id)?.value
      const wSolData = solutionData.find(sol => sol.id === item.id)?.value
      let wUserData = wUserDataStored || new Array(wSolData.length).fill(' ').join('')
  
      item.cells.forEach((c, idx) => {
        if (solutionDataTypeCells.includes(c)) {
          wUserData = `${wUserData.slice(0, idx)}${wSolData.charAt(idx)}${wUserData.slice(idx + 1, wUserData.length)}`
        }
      })
  
      return {
        id: item.id,
        activityId,
        correct: Boolean(wUserDataStored && wUserData.toLowerCase() === wSolData.toLowerCase()),
        filled: Boolean(wUserDataStored && wUserData.indexOf(' ') === -1)
      }
    })
  
    return [
      validationDataActions.addValidation({
        activityType: 'iccrossword',
        data: normalizeValidationData(validationData).entities.validationData
      })
    ]
  } else {
    return []
  }
}
