import { createAction } from '@reduxjs/toolkit'
import { dispatchEvent } from '../../../../utils/actions/eventManager'
import { eventConstants } from '../../../activities/action'

import { 
  put,
  takeEvery,
  select
} from 'redux-saga/effects'

import { normalizeUserData } from '../../../userData/schema'

import { getActivityCellById, getCellWords } from '../selector/cellsItemData'
import { getUserDataListByIds } from '../../../userData/selector/general'

import { actions as reducerActions } from '../reducer'
import { actions as userDataActions } from '../../../userData/reducer'

const setCellUserDataAction = createAction('iccrosswordSetCellData')
const clearCellUserDataAction = createAction('iccrosswordClearCellData')

function* watchSetCellUserData() {
  yield takeEvery(setCellUserDataAction.type, setCellUserData)
}

function* watchClearCellUserData() {
  yield takeEvery(clearCellUserDataAction.type, clearCellUserData)
}

function* setCellUserData(action) {
  try {
    const { cell, words, userData } = yield* selectData(action.payload)

    const userDataToFire = words.map(word => {
      const wUserData = userData.find(u => u.id === word.id)
      let cellIndex = word.cells.indexOf(cell.id)
  
      let value = cellUserValueToWordUserValue(
        action.payload.value, 
        wUserData?.value, 
        cellIndex, 
        word.cells.length
      )

      return {
        id: word.id,
        activityId: cell.activityId,
        value
      }
    })
    
    yield put(
      reducerActions.setCellsUserData(
        { 
          data: normalizeUserData([action.payload]).entities.userData
        }
      )
    )
    
    yield put(
      userDataActions.setUserData(
        {
          activityType: 'iccrossword',
          data: normalizeUserData(userDataToFire).entities.userData
        }
      ))
    dispatchEvent(eventConstants.VALUE_CHANGE, {
      type: 'iccrossword', 
      id: action.payload.activityId 
    })
  } catch (e) {
    throw Error(e.message)
  }
}

function* clearCellUserData(action) {
  try {
    const { cell, words, userData } = yield* selectData(action.payload)

    const wordsToChange = []
    const wordsToDelete = []

    words.forEach(word => {
      const wUserData = userData.find(u => u.id === word.id)
      let value
      let cellIndex = word.cells.indexOf(cell.id)

      value = cellUserValueToWordUserValue(
        ' ', 
        wUserData?.value, 
        cellIndex,
        word.cells.length
      )
      
      if (value.trim()) {
        wordsToChange.push({
          id: word.id,
          value
        })
      } else {
        wordsToDelete.push(word.id)
      }
    })
  
    yield put(
      reducerActions.clearCellsUserData(
        { 
          data:  [cell.id]
        }
      )
    )
    
    if (wordsToChange.length) {
      yield put(
        userDataActions.setUserData(
          {
            activityId: cell.activityId,
            activityType: 'iccrossword',
            data: normalizeUserData(wordsToChange).entities.userData
          }
        )
      )
    }

    if (wordsToDelete.length) {
      yield put(
        userDataActions.clearUserData(
          {
            activityId: cell.activityId,
            activityType: 'iccrossword',
            data: wordsToDelete
          }
        )
      )
    }
    dispatchEvent(eventConstants.VALUE_CHANGE, {
      type: 'iccrossword', 
      id: action.payload.activityId 
    })
  } catch (e) {
    throw Error(e.message)
  }
}

function* selectData(payload) {
  const cell = yield select(getActivityCellById, payload)
  const words = yield select(getCellWords, cell)
  const userData = yield select(getUserDataListByIds, {
    activityId: cell.activityId,
    ids: cell.words
  })
  return {
    cell,
    words,
    userData
  }
}

const cellUserValueToWordUserValue = (cellValue, wordValue, index, length) => {
  if (wordValue) {
    return `${wordValue.substr(0, index)}${cellValue}${wordValue.substr(index + 1, length - index)}`
  } else {
    let wChars = new Array(length).fill(' ')
    wChars[index] = cellValue
    return wChars.join('')
  }
}

export default {
  watchSetCellUserData,
  watchClearCellUserData
}

export const actions = {
  setCellUserData: setCellUserDataAction,
  clearCellUserData: clearCellUserDataAction
}
