import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import WithNestedActivityData from '../../../activities/components/HOC/WithNestedActivityData'
import registerActivity from '../registerActivity'
import {
  cluePositionIsInside,
  cluesInColumnsIsOn
} from '../../../screens/selector/optionValues'
import {
  getICCrosswordColSize,
  getICCrosswordRowSize
} from '../selector/general'
import { getActivityTransactionByActivityId } from '../../../activities/selector'

import ICCrosswordGrid from './ICCrosswordGrid'
import { ICCrossword as ICCrosswordUI } from './ui.jsx'
import ICCrosswordClues from './Clues/ICCrosswordClues.jsx'
import ICCrosswordHighlightManager from './managers/ICCrosswordHighlightManager.jsx'
import ICCrosswordUserDataManager from './managers/ICCrosswordUserDataManager'
import ICCrosswordFocusManager from './managers/ICCrosswordFocusManager'

const ICCrossword = ({
  activityId,
  visible,
  cluesAreInside,
  cluesInColumns,
  screenId,
  colSize,
  rowSize,
  transactions
}) => {
  return (
    <>
      {!transactions.length && visible && (
        <ICCrosswordFocusManager
          activityId={activityId}
          screenId={screenId}
          rowSize={rowSize}
          colSize={colSize}
        >
          <ICCrosswordUserDataManager
            activityId={activityId}
            screenId={screenId}
          >
            <ICCrosswordHighlightManager
              activityId={activityId}
              screenId={screenId}
            >
              <ICCrosswordUI>
                <ICCrosswordGrid
                  activityId={activityId}
                  screenId={screenId}
                  cols={colSize}
                  rows={rowSize}
                />
                {!cluesAreInside && (
                  <ICCrosswordClues
                    activityId={activityId}
                    screenId={screenId}
                    cluesInColumns={cluesInColumns}
                  />
                )}
              </ICCrosswordUI>
            </ICCrosswordHighlightManager>
          </ICCrosswordUserDataManager>
        </ICCrosswordFocusManager>
      )}
    </>
  )
}

ICCrossword.propTypes = {
  activityId: PropTypes.string.isRequired,
  visible: PropTypes.bool,
  cluesAreInside: PropTypes.bool.isRequired,
  cluesInColumns: PropTypes.bool.isRequired,
  screenId: PropTypes.number.isRequired,
  colSize: PropTypes.number,
  rowSize: PropTypes.number,
  transactions: PropTypes.array
}

const mapStatetoProps = (state, ownProps) => ({
  cluesAreInside: cluePositionIsInside(state, ownProps),
  cluesInColumns: cluesInColumnsIsOn(state, ownProps),
  colSize: getICCrosswordColSize(state, ownProps),
  rowSize: getICCrosswordRowSize(state, ownProps),
  transactions: getActivityTransactionByActivityId(state, ownProps)
})

export default WithNestedActivityData(
  connect(mapStatetoProps, null)(ICCrossword),
  registerActivity
)
