import React, { useEffect } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { getActivityByActivityId } from '../../selector/index'

const WithNestedActivityData = (WrappedComponent, registerActivity) => {
  const WithNestedActivityDataComp = (props) => {
    const copy = { ...props }
    const { wrappedActivityData, activityType, activityId } = props
    delete copy['wrappedActivityData']

    useEffect(() => {
      registerActivity({
        activityType,
        activityId
      })
    }, [])

    let mapProps = wrappedActivityData || {}

    if (wrappedActivityData) {
      return <WrappedComponent {...copy} {...mapProps} />
    } else {
      return null
    }
  }

  WithNestedActivityDataComp.propTypes = {
    wrappedActivityData: PropTypes.shape({
      screenId: PropTypes.number,
      activityId: PropTypes.string,
      activityType: PropTypes.string
    })
  }

  const mapStateToProps = (state, ownProps) => ({
    wrappedActivityData: getActivityByActivityId(state, ownProps)
  })

  return connect(mapStateToProps, null)(WithNestedActivityDataComp)
}

export default WithNestedActivityData
