import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import { ICCrosswordCellInput as ICCrosswordCellInputUI } from '../ui.jsx'

import { 
  ICCrosswordUserDataContext,
  ICCrosswordFocusContext, 
  ICCrosswordHighlightContext 
} from '../../context'

import {
  caseIsLower
} from '../../../../screens/selector/optionValues'

import { getCellWordsValidationData } from '../../selector/cellsValidationData'
import { getCellSolutionData } from '../../selector/cellsSolutionData'
import { getCellUIDataById, getCellWordsUIData } from '../../selector/cellsUIData'
import { getCellUserData } from '../../selector/cellsUserData'
import { getCellWords } from '../../selector/cellsItemData'

const ICCrossworCellInput = (props) => {  
  const {
    id,
    lowercase,
    words,
    solutionData,
    uiData,
    wordsUIData = [],
    wordsValidationData = [],
    userData
  } = props
  const [orientations, setOrientations] = useState([])
  const [label, setLabel] = useState()

  const letter = uiData.dataType === 'solution' ? solutionData : userData

  useEffect(() => {
    let cellLabel
    words.forEach(w => {
      if (w.cells.indexOf(id) === 0) {
        cellLabel = w.label
      }
    })
    setLabel(cellLabel)
  }, [words, id])

  useEffect(() => {
    const orient = words.map(word => {
      const { cells, label, align } = word
      const wordId = word.id
      
      let relativePosition = 'middle'
      if (cells.indexOf(id) === 0) {
        relativePosition = 'first'
      } else if (cells.indexOf(id) === cells.length - 1) {
        relativePosition = 'last'
      }

      const wValidation = wordsValidationData.find(vd => vd.id === wordId)
      const wUIData = wordsUIData.find(uid => uid.id === wordId)
    
      const retValue = {
        relativePosition,
        orientation:  align === 'v' ? 'vertical' : 'horizontal',
        wordLabel: label,
        length: cells.length,
        position: cells.indexOf(id),
        classes: wUIData?.extra || []
      }
      
      if (wUIData?.validationType && wValidation?.hasOwnProperty('correct')) {
        retValue.isCorrect = wValidation.correct
      }

      if (wUIData?.validationType === 'correct_incorrect_empty' && wValidation?.hasOwnProperty('filled')) {
        retValue.isFilled = wValidation.filled
      }

      return retValue
    })

    setOrientations(orient)
  }, [words, wordsValidationData, uiData])

  return (
    <ICCrosswordUserDataContext.Consumer>
      { userContext => (
        <ICCrosswordFocusContext.Consumer>
          { focusContext => (
            <ICCrosswordHighlightContext.Consumer>
              { highlightContext => ( 
                <ICCrosswordCellInputUI 
                  letter={letter}
                  enabled={uiData.enabled}
                  label={label}
                  lowerCase={lowercase}
                  focused={focusContext.focused === id}
                  highlighted={highlightContext.highlighted?.includes(id)}
                  orientations={orientations}
                  onKey={(evt) => {
                    userContext.cellKeyEvt(id, evt.type, evt.keyCode)
                    focusContext.keyboardEvent(id, evt.type, evt.keyCode, evt.shiftKey)
                  }}
                  onClick={() => {
                    focusContext.clickEvent(id)
                  }}
                  customEvents={[{
                    name: 'specialchars:inserted',
                    callback: (evt) => {
                      userContext.cellChange(id, evt.value)
                      focusContext.changeEvent(id)
                    }
                  }]}
                />
              )}
            </ICCrosswordHighlightContext.Consumer>
          )}
        </ICCrosswordFocusContext.Consumer>
      )}
    </ICCrosswordUserDataContext.Consumer>
  )
}

ICCrossworCellInput.propTypes = {
  id: PropTypes.string,
  lowercase:PropTypes.bool,
  words: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string,
    label: PropTypes.number
  })),
  uiData: PropTypes.shape({
    id: PropTypes.string,
    enabled: PropTypes.bool,
    dataType: PropTypes.string
  }),
  wordsUIData: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string,
    validationType: PropTypes.string,
    extra: PropTypes.array
  })),
  wordsValidationData: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string,
    correct: PropTypes.bool,
    filled: PropTypes.bool
  })),
  solutionData: PropTypes.string,
  userData:PropTypes.string
}

const mapStateToProps = (state, ownProps) => ({
  words: getCellWords(state, ownProps),
  uiData: getCellUIDataById(state, ownProps),
  userData: getCellUserData(state, ownProps),
  wordsValidationData: getCellWordsValidationData(state, ownProps),
  wordsUIData: getCellWordsUIData(state, ownProps),
  solutionData: getCellSolutionData(state, ownProps),
  lowercase: caseIsLower(state, ownProps)
})

export default connect(
  mapStateToProps
)(ICCrossworCellInput)
