import React from 'react'
import PropTypes from 'prop-types'
import { ICCrosswordHighlightContext } from '../../context'
 
import { ICCrosswordClue as ICCrosswordClueUI } from '../ui.jsx'

const ICCrossworClue = ({ 
  label, 
  clue, 
  id
}) => {

  return (
    <ICCrosswordHighlightContext.Consumer>
      {highlightContext => (    
        <ICCrosswordClueUI 
          label={label} 
          clue={clue}
          onMouseOver={() => highlightContext.mouseOverEvent([id])}
          onMouseOut={() => highlightContext.mouseOutEvent()}
        />
      )}
    </ICCrosswordHighlightContext.Consumer>
  )
}

ICCrossworClue.propTypes = {
  label: PropTypes.number.isRequired,
  clue: PropTypes.string.isRequired,
  id: PropTypes.string.isRequired
}

export default ICCrossworClue
