import createCachedSelector from 're-reselect'
import { createArraySelector } from '../../../../selector/utils'

import { getActivityCells } from './cellsItemData'
import { getActivityItemData } from '../../../itemData/selector/general'
import { getActivityUIData } from '../../../uiData/selector/general'

const getState = (state) => state.iccrossword_cells_uiData
const getId = (state, props) => props.id
const getActivityId = (state, props) => props.activityId 
const getWords = (state, props) => props.words

const getCellsUIDataAsList = createArraySelector(
  [getState],
  (cells) => {
    if (!cells) {
      return
    }
    return Object.values(cells)
  }
)

export const getActivityCellsUIData = createCachedSelector(
  [getCellsUIDataAsList, getActivityId],
  (cellsUIData, activityId) => {
    if (!cellsUIData || !activityId) {
      return
    }

    return cellsUIData.filter(cell => cell.activityId === activityId)
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getCellUIDataById = createCachedSelector(
  [getState, getId],
  (uiData, id) => {
    if (!id || !uiData) {
      return
    }
    return uiData[id]
  }
)(getId)

export const getSolutionDataTypeCells = createCachedSelector(
  [getActivityCellsUIData, getActivityCells],
  (cellsUIData, cells) => {
    if (!cellsUIData || !cellsUIData.length || !cells || !cells.length) {
      return
    }
    const solutionDataCellsIds = cellsUIData
      .filter(({ dataType }) => dataType === 'solution')
      .map(({ id }) => id)

    return cells.filter(cell => solutionDataCellsIds.includes(cell.id))
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getEnabledCells = createCachedSelector(
  [getActivityCellsUIData, getActivityCells],
  (cellsUIData, cells) => {
    if (!cellsUIData || !cellsUIData.length || !cells || !cells.length) {
      return
    }
    const enabledCells = cellsUIData
      .filter(({ enabled }) => enabled )
      .map(({ id }) => id)

    return cells.filter(cell => enabledCells.includes(cell.id))
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getEnabledCellsWords = createCachedSelector(
  [getEnabledCells, getActivityItemData],
  (cells, words) => {
    if (!cells || !words || !words.length) {
      return
    }

    const cellWordsIds = [...new Set(cells.map(({ words }) => words).flat())]

    return words.filter(({ id }) => cellWordsIds.includes(id))
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getCellWordsUIData = createCachedSelector(
  [getWords, getActivityUIData],
  (cellWords, uiData) => {
    if (!cellWords?.length || !uiData) {
      return
    }
    return uiData.filter(v => cellWords.includes(v.id)) 
  }
)(getId, { selectorCreator: createArraySelector })
