import { select } from 'redux-saga/effects'

import { actions } from '../../reducer'
import { actions as userDataActions } from '../../../../userData/reducer'

import { getItemDataListByIds } from '../../../../itemData/selector/general'
import { getUserDataListByIds } from '../../../../userData/selector/general'

import { normalizeUserData } from '../../../../userData/schema'

import { processors as userDataProcessors } from '../../../../userData/saga/processor'

import { getWordCells } from '../../selector/cellsItemData'

const at = 'iccrossword'

export function* setUserData(payload) {
  const words = yield select(getItemDataListByIds, {
    ids: payload.data.map(d => d.id)
  })
  const ret = userDataProcessors.setUserData(payload)
  const cells = []
  payload.data.forEach(({ id, value }) => {
    const word = words.find(w => w.id === id)
    const cellVals = value.split('')

    word.cells.forEach((cell, idx) => {
      if (cellVals[idx].trim() && !cells.find(({ id }) => id === cell)) {
        cells.push({
          id: cell,
          activityId: payload.activityId,
          value: cellVals[idx]
        })
      }
    })
  })
  if (cells.length) {
    ret.push(actions.setCellsUserData({
      data: normalizeUserData(cells).entities.userData
    }))
  }
  return ret
}

export function* clearUserData(payload) {
  try {
    const words = yield select(getItemDataListByIds, payload)
    const wordsUserData = yield select(getUserDataListByIds, payload)
    const wordsIds = words.map(({ id }) => id)
    const ret = []
    let cells = []
  
    const wordIdsToDelete = []
    const wordsToChange = []
  
    yield* words.map(function*(word) {
      const wordCells = yield select(getWordCells, word)
      let userData = wordsUserData.find(({ id }) => id === word.id)?.value.split('')
  
      const crossedWords = wordCells.map(c => c.words).flat()
      if (crossedWords.every(w => wordsIds.includes(w))) {
        wordIdsToDelete.push(word.id)
      } else {
        const cellsToClear = wordCells.filter(c => c.words.every(w => wordsIds.includes(w)))
        cells = cells.concat(cellsToClear.map(( { id } ) => id))
        
        if (userData?.length) {
          userData = userData.map((char, idx) => cellsToClear.includes(wordCells[idx]) && ' ' || char).join('')
          if (userData.trim()) {
            wordsToChange.push({
              id: word.id,
              activityId: word.activityId,
              value: userData
            })
          } else {
            wordIdsToDelete.push(word.id)
          }
        }
      }
    })
    if (wordIdsToDelete.length) {
      ret.push(userDataActions.clearUserData({
        activityType: at,
        data: wordIdsToDelete
      }))
    }
    if (wordsToChange.length) {
      ret.push(userDataActions.setUserData({
        activityType: at,
        data: normalizeUserData(wordsToChange).entities.userData
      }))
    }
    if (cells.length) {
      ret.push(actions.clearCellsUserData({
        data: [... new Set(cells)]
      }))
    }
    return ret
  } catch (e) {
    throw new Error(e.message)
  }
}
