import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { actions } from '../../saga'
import { ICCrosswordUserDataContext } from '../../context'
import { isBackspace, isText } from '../../../../activities/utils/charCodeUtils'

const ICCrosswordUserDataManager = ({
  setUserData, 
  removeUserData,
  children
}) => {
  const cellKeyEvt = (id, evtType, keyCode) => {
    if (evtType === 'keydown') {
      let value
      if (isText(keyCode)) {
        value = String.fromCharCode(keyCode)
      } else if (isBackspace(keyCode)) {
        value = ''
      }
      if (value !== undefined) {
        if (value) {
          setUserData({
            id,
            value
          })
        } else {
          removeUserData({
            id
          })
        }
      }
    }
  }

  const cellChange = (id, value) => {
    if (value) {
      setUserData({
        id,
        value
      })
    } else {
      removeUserData({
        id
      })
    }
  }

  return (
    <ICCrosswordUserDataContext.Provider value={{
      cellKeyEvt,
      cellChange
    }}>
      {children}
    </ICCrosswordUserDataContext.Provider>
  )
}

ICCrosswordUserDataManager.propTypes = {
  setUserData: PropTypes.func.isRequired,
  removeUserData: PropTypes.func.isRequired,
  children: PropTypes.object
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  setUserData: (cell) => dispatch(actions.setCellUserData({
    ...cell,
    activityId: ownProps.activityId
  })),
  removeUserData: (cell) => dispatch(actions.clearCellUserData({
    ...cell,
    data:[cell.id]
  }))
})

export default connect(
  null,
  mapDispatchToProps
)(ICCrosswordUserDataManager)
