import { injectReducer, injectSagas } from '../store'

const registeredActivityTypes = {}

export const registerActivityType = ({
  activityType, 
  reducer,
  saga
}) => {
  if (reducer) {
    injectReducer(reducer)
  }
  if (saga) {
    injectSagas(saga)
  }

  registeredActivityTypes[activityType] = true
}

export const getActivityTypeIsRegistered = (activityType) => {
  return Boolean(registeredActivityTypes[activityType])
}
