import createCachedSelector from 're-reselect'
import sortBy from 'lodash/sortBy'

import { createArraySelector } from '../../../../selector/utils'
import { cluePositionIsInside } from '../../../screens/selector/optionValues'

import { 
  getICCrosswordColSize
} from './general'

import { getActivityItemData } from '../../../itemData/selector/general'

const getCells = (state) => state.iccrossword_cells_itemData
const getActivityId = (state, props) => props.activityId 
const getId = (state, props) => props.id
const getRowIndex = (state, props) => props.index
const getWords = (state, props) => props.words
const getX = (state, props) => props.x
const getY = (state, props) => props.y
const getCellsIds = (state, props) => props.cells
const getCellsIdsJSON = (state, props) => JSON.stringify([...(props.cells || [])].sort())

const getCellsAsList = createArraySelector(
  [getCells],
  (cells) => {
    if (!cells) {
      return
    }
    return Object.values(cells)
  }
)

export const getActivityCells = createCachedSelector(
  [getCellsAsList, getActivityId],
  (cells, activityId) => {
    if (!cells || !activityId) {
      return
    }
    return cells.filter(cell => cell.activityId === activityId)
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getActivityCellById = createCachedSelector(
  [getCells, getId],
  (cells, id) => {
    if (!cells || !id) {
      return
    }
    return cells[id]
  }
)(getId)

const getWordsClueCells = createCachedSelector(
  [getActivityItemData, cluePositionIsInside],
  (items, cluesAreInside) => {
    if (!items) {
      return
    } else if (!cluesAreInside) {
      return []
    } else {
      const clues = []
      items.forEach(item => {
        const clue = {
          x: item.x,
          y: item.y,
          type: 'clue',
          words: [item.id]
        }

        if (item.align === 'v') {
          clue.y--
        } else {
          clue.x--
        }
        const storedClue = clues.find(c => c.x === clue.x - 1 && c.y === clue.y)
        if (storedClue) {
          storedClue.push(item.id)
        } else {
          clues.push(clue)
        }
      })
      return clues
    }
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getActivityCellsByRow = createCachedSelector(
  [getActivityCells, getWordsClueCells, getRowIndex, getICCrosswordColSize, getActivityId],
  (cells, cellClues, index, cols) => {
    if (!cells || index === undefined || !cellClues || !cols) {
      return
    }

    let xAdd = 0
    let yAdd = 0 
    if (cellClues.length) {
      xAdd = sortBy(cellClues, 'x')[0].x >= 0 ? 0 : 1
      yAdd = sortBy(cellClues, 'y')[0].y >= 0 ? 0 : 1
    }
    const findByY = ({ y }) => y === index - yAdd
    let rowCells = cells.filter(findByY)
    rowCells = rowCells.concat(cellClues.filter(findByY))
    rowCells = sortBy(rowCells, 'x')
    const values = new Array(cols + xAdd).fill(null)
    rowCells.forEach(cell => {
      values[cell.x + xAdd] = cell
    })

    return values
  }
)((state, { activityId, index }) => {
  return `${activityId}${index}`
})

export const getCellWords = createCachedSelector(
  [getWords, getActivityItemData],
  (cellWords, words) => {
    if (!cellWords?.length || !words) {
      return
    }
    return words.filter(w => cellWords.includes(w.id))
  }
)((state, props) => {
  return JSON.stringify([getActivityId(state, props), getX(state, props), getY(state, props)])
}, { selectorCreator: createArraySelector })

export const getWordCells = createCachedSelector(
  [getCells, getCellsIds],
  (cells, ids) => {
    if (!cells || !ids) {
      return
    }
    return ids.map(id => cells[id])
  }
)(getCellsIdsJSON, { selectorCreator: createArraySelector })
