import { select } from 'redux-saga/effects'

import { actions } from '../../reducer'

import { processors as uiDataProcessor } from '../../../../uiData/saga/processor'

import { getActivityUIData } from '../../../../uiData/selector/general'
import { getItemDataListByIds } from '../../../../itemData/selector/general'
import {
  getActivityCellById
} from '../../selector/cellsItemData'

export function* setEnabled(payload) {
  const ret = yield uiDataProcessor.setEnabled(payload)
  const cellsActionData = yield _getCellActions(payload)
  return ret.concat(cellsActionData)
}

export function* setDataType(payload) {
  const ret = yield uiDataProcessor.setDataType(payload)
  const cellsActionData = yield _getCellActions(payload)
  return ret.concat(cellsActionData)
}

export function* setBatchUI(payload) {
  const ret = yield uiDataProcessor.setBatchUI(payload)
  const cellsActionData = yield _getCellActions(payload)
  return ret.concat(cellsActionData)
}

function* _getCellsAffectedByPayload(payload) {
  const words = yield select(getItemDataListByIds, {
    ids: payload.ids
  })
  return words.map(({ cells }) => cells).flat()
}

const _allWordsInPayload = (cell, ids) => {
  return cell.words.every(({ id }) => ids.includes(id))
}

const _getWordsUINotInPayload = (cell, ids, uiData) => {
  return cell.words
    .filter(({ id }) => !ids.includes(id))
    .map((id) => uiData.find((el) => el.id === id))
}

const _enabledHash = (enabled, cell, ids, uiData) => {
  const ret = {}
  const wordsNotInPayloadUI = _getWordsUINotInPayload(cell, ids, uiData)
  if (
    !enabled ||
    _allWordsInPayload(cell, ids) ||
    wordsNotInPayloadUI.every((wordUI) => wordUI.enabled === enabled)
  ) {
    ret.enabled = enabled
  }
  return ret
}

const _dataTypeHash = (dataType, cell, ids, uiData) => {
  const ret = {}
  const wordsNotInPayloadUI = _getWordsUINotInPayload(cell, ids, uiData)
  if (
    dataType === 'solution' ||
    _allWordsInPayload(cell, ids) ||
    wordsNotInPayloadUI.every((wordUI) => wordUI.dataType === dataType)
  ) {
    ret.dataType = dataType
  }
  return ret
}

function* _getCellActions(payload) {
  let { activityId, ids, data, enabled, validationType, dataType, extra } = payload

  data = data || {
    enabled,
    validationType,
    dataType,
    extra
  }
  const cells = yield _getCellsAffectedByPayload(payload)

  const uiData = yield select(getActivityUIData, { activityId })

  const cellsMappedToPayload = []
  yield* cells.map(function* (id) {
    const cell = yield select(getActivityCellById, { id })

    let hash = {}
    if (data.enabled !== undefined) {
      hash = Object.assign(hash, _enabledHash(data.enabled, cell, ids, uiData))
    }
    if (data.dataType !== undefined) {
      hash = Object.assign(
        hash,
        _dataTypeHash(data.dataType, cell, ids, uiData)
      )
    }

    cellsMappedToPayload.push({
      ...hash,
      id,
      activityId
    })
  })

  return [actions.setCellUIData({ data: cellsMappedToPayload })]
}
