import createCachedSelector from 're-reselect'

import { createArraySelector } from '../../../../selector/utils'
const getCellsUserData = (state) => state.iccrossword_cells_userData
const getActivityId = (state, props) => props.activityId 
const getId = (state, props) => props.id 

const getCellsUserDataAsList = createArraySelector(
  [getCellsUserData],
  (cells) => {
    if (!cells) {
      return
    }
    return Object.values(cells)
  }
)
export const getActivityCellsUserData = createCachedSelector(
  [getCellsUserDataAsList, getActivityId],
  (cells, activityId) => {
    if (!cells || !activityId) {
      return
    }
    return cells.filter(cell => cell.activityId === activityId)
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getCellUserData = createCachedSelector(
  [getId, getCellsUserData],
  (id, userDataCells) => {
    if (!id) {
      return
    }
    return userDataCells[id]?.value
  }
)(getId)
