import React, { useState, useEffect, useCallback } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import sortBy from 'lodash/sortBy'
import findIndex from 'lodash/findIndex'

import { getEnabledCellsWords } from '../../selector/cellsUIData'
import { getEnabledCells } from '../../selector/cellsUIData'

import { ICCrosswordFocusContext } from '../../context'
import { keyCodes, isArrowCode, isBackspace, isText, isTab } from '../../../../activities/utils/charCodeUtils'

const ICCrosswordFocusManager = ({ 
  editableCells, 
  editableWords, 
  rowSize, 
  colSize, 
  children 
}) => {

  const [focusedWord, setFocusedWord] = useState()

  const [focusedCell, setFocusedCell] = useState()

  const getCellByPosition = ({ x, y }) => {
    return editableCells.find(cell => cell.x === x && cell.y === y)
  }

  const getEditableCellsInWord = (word) => {
    return word.cells
      .map(id => editableCells.find(c => c.id === id))
      .filter(Boolean)
  }

  const getCellWordsOrderedByAlign = (cell) => {
    return sortBy(
      cell.words
        .map(id => editableWords.find(w => w.id === id))
        .filter(Boolean),
      'align'
    )
  }

  const orderedWords = sortBy(editableWords, 'align')
  
  const findNextEditableCellByArrow = ({ xStep, yStep }, cell) => {
    let xpos = cell.x
    let ypos = cell.y
    let nextCell
    do {
      xpos += xStep
      ypos += yStep

      nextCell = getCellByPosition({ x:xpos, y:ypos })
      if (nextCell) {
        cell = nextCell
        break
      }
    } while (xpos >= 0 && xpos <= rowSize && ypos >= 0 && ypos <= colSize)
    return cell
  }

  const findNextEditableCellInWord = (word, cell, direction) => {
    if (word.cells.includes(cell.id)) {
      const wordEditCells = getEditableCellsInWord(word)
      const idxCellWord = findIndex(wordEditCells, c => c.id === cell.id)
      let newidxCellWord = idxCellWord + direction

      if (newidxCellWord < 0 || newidxCellWord >= wordEditCells.length) {
        return null
      } else {
        return wordEditCells[newidxCellWord]
      }
    }
  }

  const findNextEditableWordAndCell = (word, cell, direction) => {
    let nextWord = word
    let nextCell = findNextEditableCellInWord(word, cell, direction)

    if (!nextCell) {
      const idxWord = findIndex(orderedWords, w => w.id === word.id)
      nextWord = orderedWords[( idxWord + orderedWords.length + direction ) % orderedWords.length]
      const wordEditCells = getEditableCellsInWord(nextWord)
      const idxCellWord = direction === 1 ? 0 : wordEditCells.length - 1
      nextCell = wordEditCells[idxCellWord]
    }
    return [nextCell, nextWord]
  }

  const checkForArrowKeyEvt = (id, keyCode ) => {
    let nextCell, nextWord

    if (isArrowCode(keyCode)) {
      let xStep = 0
      let yStep = 0

      switch (keyCode) {
        case keyCodes.UP:
          yStep = -1
          break
        case keyCodes.DOWN:
          yStep = 1
          break
        case keyCodes.LEFT:
          xStep = -1
          break
        case keyCodes.RIGHT:
          xStep = 1
          break
      }
      nextCell = findNextEditableCellByArrow({ xStep, yStep }, id)
      if (nextCell) {
        nextWord = getCellWordsOrderedByAlign(nextCell)[0]
      }
    }
    return [nextCell, nextWord]
  }

  const checkForKeyEvent = (cell, word, keyCode, shift) => {
    let nextCell, nextWord
    const back = isBackspace(keyCode)
    const text = isText(keyCode)
    const tab = isTab(keyCode)

    if (back || text || tab) {
      let direction = (back || (tab && shift)) ? -1 : 1;
      [nextCell, nextWord] = findNextEditableWordAndCell(word, cell, direction)
    }
    return [nextCell, nextWord]
  }

  const keyboardEvent = useCallback((id, evtType, keyCode, shift) => {
    let [cell, word] = getFocusedCellAndWord(id)
    let nextCell, nextWord
    if (evtType === 'keyup') {
      [nextCell, nextWord] = checkForArrowKeyEvt(cell, keyCode)
    } else if (evtType === 'keydown') {
      [nextCell, nextWord] = checkForKeyEvent(cell, word, keyCode, shift)
    }

    if (nextCell && nextWord) {
      updateFocusedCellAndWord(nextCell, nextWord)
    }
  })

  const changeEvent = useCallback((id) => {
    let [cell, word] = getFocusedCellAndWord(id)
    let [nextCell, nextWord] = findNextEditableWordAndCell(word, cell, 1)
    if (nextCell && nextWord) {
      updateFocusedCellAndWord(nextCell, nextWord)
    }
  })

  const clickEvent = useCallback((id) => {
    const cell = editableCells.find(c => c.id === id)
    updateFocusedCellAndWord(
      cell, 
      getCellWordsOrderedByAlign(cell)[0]
    )
  })

  const getFocusedCellAndWord = (id) => {
    let word 
    let cell
    if (id === focusedCell) {
      cell = editableCells.find(c => c.id === focusedCell)
      word = editableWords.find(w => w.id === focusedWord)
      word = word || getCellWordsOrderedByAlign(cell)[0]
    } else {
      cell = editableCells.find(c => c.id === id)
      word = getCellWordsOrderedByAlign(cell)[0]
    }
    return [cell, word]
  }

  const updateFocusedCellAndWord = (cell, word) => {
    setFocusedCell(cell.id)
    setFocusedWord(word.id)
  }
  
  useEffect(() => {
    if (editableCells.length && !editableCells.find(({ id }) => id === focusedCell)) {
      const _1EditCell = editableCells[0]
      const _1EditWord = getCellWordsOrderedByAlign(_1EditCell)[0]
  
      setFocusedWord(_1EditWord?.id)
      setFocusedCell(_1EditCell?.id)
    }
  }, [editableCells, focusedCell])

  return (
    <ICCrosswordFocusContext.Provider value={{
      focused: focusedCell,
      keyboardEvent,
      clickEvent,
      changeEvent
    }}>
      {children}
    </ICCrosswordFocusContext.Provider>
  )
}

ICCrosswordFocusManager.propTypes = {
  rowSize: PropTypes.number.isRequired,
  colSize: PropTypes.number.isRequired,
  editableCells: PropTypes.array.isRequired,
  editableWords: PropTypes.array.isRequired,
  children: PropTypes.node
}

const mapStateToProps = (state, ownProps) => ({
  editableCells: getEnabledCells(state, ownProps),
  editableWords: getEnabledCellsWords(state, ownProps)
})

export default connect(
  mapStateToProps
)(ICCrosswordFocusManager)
