import { register as registerActivitiesProcessor } from '../../../../activities/saga/processor'
import { register as registerValidationDataProcessor } from '../../../../validationData/saga/processor'
import { register as registerUserDataProcessor } from '../../../../userData/saga/processor'
import { register as registerUIDataProcessor } from '../../../../uiData/saga/processor'

import { addActivities, removeActivities } from './activity'
import { validate } from './validationData'
import { setUserData, clearUserData } from './userData'
import { setEnabled, setDataType, setBatchUI } from './uiData'

const at = 'iccrossword'

export const registerProcessors = () => {
  registerActivitiesProcessor('addActivities', at, addActivities)
  registerActivitiesProcessor('removeActivities', at, removeActivities)
  registerValidationDataProcessor('validate', at, validate),
  registerUserDataProcessor('setUserData', at, setUserData)
  registerUserDataProcessor('clearUserData', at, clearUserData)
  registerUIDataProcessor('setEnabled', at, setEnabled)
  registerUIDataProcessor('setDataType', at, setDataType)
  registerUIDataProcessor('setBatchUI', at, setBatchUI)
}
