import { register as registerItemDataProcessor } from '../../../itemData/selector'
import { register as registerUserDataProcessor } from '../../../userData/selector'

import { 
  getActivityUserDataFilled, 
  getActivityItemDataFilteredByFilled
} from './general'

const at = 'iccrossword'

export const registerProcessors = () => {
  registerItemDataProcessor('getActivityItemDataFilteredByFilled', at, getActivityItemDataFilteredByFilled)
  registerUserDataProcessor('getActivityUserDataFilled', at, getActivityUserDataFilled)
}
